.preloader {
    /*position: fixed;*/
    position: center;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: #fff;
    z-index: 1001;
}

.preloader__image {
    position: relative;
    top: 50%;
    left: 40%;
    width: 64px;
    height: 64px;
    /*margin-top: -32px;*/
    /*margin-left: -32px;*/
    background: url('Preloader_2.gif') no-repeat 50% 50%; /*расположение (url) изображения gif и др. параметры*/
}

.loaded_hiding .preloader {
    transition: 0.3s opacity;
    opacity: 0;
}

.loaded .preloader {
    display: none;
}

.preloaderContainer {
    height: 100px;
    width: 100px;
}